@use '../abstract/colors' as *;
@use '../abstract/function' as *;
@use '../abstract/font' as *;
@use "../abstract/mixins" as *;
@use "../abstract/breakpoints" as *;

.loader{
    width:100%;
    height:100vh;
    position:fixed;
    top:0px;
    left:0px;
    background-color:$clr-primary;
    z-index:999999;
}

.loader.end{
    user-select: none;
    pointer-events: none;
    transform:translateY(-100vh);
    transition: cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.7s 0.6s;
}

.loader__indication{
    position:absolute;
    right:rem(40);
    bottom:rem(48);
    color:$clr-white;
    font-size:rem(20);
    font-family:$ff-secondary;
    z-index:3;
    @include breakpoint-md{
        bottom:rem(40);  
    }
}

.loader__wrapper-one{
    z-index:1;
    background-color:$clr-primary;
}

.loader__wrapper-one.active{
    width:100%;
}

.loader__wrapper-one , .loader-wrapper-two{
    position:absolute;
    top:0;
    left: 0;
    width:0%;
    height:100%;
    transition:1s all ease-in-out;
}


