@use "../abstract/function" as *;
@use "../abstract/breakpoints" as *;

$full-width:100%;

// ========= page container starts =========
.page-container {
    max-width:none;
    margin:0 rem(16);

    @include breakpoint-sm{
        margin:0 rem(20);
    }
    
    @include breakpoint-md{
        max-width:calc($full-width*0.94);
        margin:0 auto;
    }
  
    @include breakpoint-lg{
        max-width:calc($full-width*0.92);
    }

    @include breakpoint-xl{
        max-width:calc($full-width*0.91);
    }

    @include breakpoint-xxl{
        max-width:calc($full-width*0.925);
    }
    @include breakpoint-xxxl{
        max-width:calc($full-width*0.91667);
    }
}

// page container with no margin on left
.page-container--ml{
    @include breakpoint-md{
        max-width:calc($full-width*0.958);
        margin-left:auto;
        margin-right:0;
    }
}


// page container with no margin on right
.page-container--mr{
    @include breakpoint-md{
        max-width:calc($full-width*0.958);
        margin-left:auto;
        margin-right:0;
    }
  
    @include breakpoint-lg{
        max-width:calc($full-width*0.93775);
    }

    @include breakpoint-xl{
        max-width:calc($full-width*0.9375);
    }

    @include breakpoint-xxl{
        max-width:calc($full-width*0.8277);
    }
}
